import React from 'react';

// components
import About from '../About'
import MintButton from '../MintButton'
import Gallery from '../Gallery'
import RecentGallery from '../RecentGallery'
import Footer from '../Footer'

let IMG = ""

function Home(props) {
  if(props.addresses) {
    return (
      <div className="flex flex-col items-center w-full">
        <div className="card w-2/6 lg:w-1/6">
          <img className="gallery-img" src={"data:image/svg+xml;base64,"+IMG} alt="NFT" />
        </div>
        <div className="card w-3/5">
          <About {...props} />
        </div>
        <MintButton {...props} />
        <div className="card w-3/5">
          <Gallery {...props} />
        </div>
        <div className="card w-3/5">
          <RecentGallery {...props} />
        </div>
        <div className="w-3/5">
          <Footer {...props} />
        </div>
      </div>
    )
  } else {
    return (
      <div className="flex flex-col items-center w-full">
        <div className="card w-2/6 lg:w-1/6">
          <img tokenid="12" className="gallery-img" src={"data:image/svg+xml;base64," + IMG} alt="NFT" />
        </div>
        <div className="card w-3/5">
          <About {...props} />
        </div>
        <div className="card w-3/5">
          <p className="text-center">Connect to Metis Stardust</p>
        </div>
        <div className="w-3/5">
          <Footer {...props} />
        </div>
      </div>
    )
  }
}
 
export default Home;